	module.exports = {"data":{"wordpressPage":{"title":"Sample Page","content":"<p>This is an example page. It&#8217;s different from a blog post because it will stay in one place and will show up in your site navigation (in most themes). Most people start with an About page that introduces them to potential site visitors. It might say something like this:</p>\n<blockquote><p>Hi there! I&#8217;m a bike messenger by day, aspiring actor by night, and this is my website. I live in Los Angeles, have a great dog named Jack, and I like pi&#241;a coladas. (And gettin&#8217; caught in the rain.)</p></blockquote>\n<p>&#8230;or something like this:</p>\n<blockquote><p>The XYZ Doohickey Company was founded in 1971, and has been providing quality doohickeys to the public ever since. Located in Gotham City, XYZ employs over 2,000 people and does all kinds of awesome things for the Gotham community.</p></blockquote>\n<p>As a new WordPress user, you should go to <a href=\"http://localhost:8888/content-otter/wp-admin/\">your dashboard</a> to delete this page and create new pages for your content. Have fun!</p>\n","date":"January 22, 2017"},"site":{"id":"Site","siteMetadata":{"title":"Email Otter","subtitle":"WordPress Gatsby Theme"}}},"pathContext":{"id":"147ce629-51d8-5b6f-97a5-bd0c69893bd4"}}

